.dishPage {
   position: relative;
   min-height: 85vh;
   background-repeat: repeat;
   background-color: #070707;
   background-color: #000000;
   background-position: top;
   background-image: url("https://www.transparenttextures.com/patterns/white-wall-3-2.png");
   background-attachment: none;

   @media screen and (min-width: 767.9px) {
     };
     @media screen and (min-width: 1200px) {
      background-attachment: fixed;
     };

}

.menuCardMobile {

   @media screen and (max-width: 767.9px) {
      width: 70px;
      height: 70px;
     };

}
.menuCardImage {
   @media screen and (max-width: 767.9px) {
      display: none;
     };
}

.dishPageContainer {
   padding: 0 10px;
  
   @media screen and (min-width: 767.9px) {
    padding: 0 120px;
   };
   @media screen and (min-width: 1200px) {
    padding: 0 250px;
   };
  }

.discriptionBox {
 display: flex;
 position: relative;
 justify-content: center;
 align-items: center;

}


.dishTitle {
   display: flex;
   justify-content: center;
   align-items: center;
   height: 95px;
   
   font-size: 15px;
   margin: 0;
   color: #7c7c7c;
/* color: transparent; */
   @media screen and (min-width: 768px) {
     
   };
   @media screen and (min-width: 1200px) {
    
   };
}

.dishPageBox {
   display: grid;
   width: auto;
   height: auto;
   
  @media screen and (min-width: 767.9px) {
       
  };
  @media screen and (min-width: 1200px) {
    
  };
  }

.dishList {
   display: grid;
   /* width: 100%; */
   grid-template-columns: repeat(1, 1fr);
   grid-row-gap: 10px;
   border: none;
   padding: 20px 10px;
   margin-bottom: 70px;
   background: #00000000;
   border-radius: 19px;
   box-shadow:
   inset rgba(42, 41, 41, 0.471) -5px -5px 9px,
   inset #303030 5px 5px 9px,
   rgb(0, 0, 0) 3px 3px 8px -3px;
   z-index: 9;
      
   @media screen and (min-width: 767.9px) {
      padding: 30px 15px;
   };

  @media screen and (min-width: 1200px) {
   padding: 40px 20px;
   /* margin-bottom: 50px; */
  };
}



.dishListTitle {
   display: flex;
   justify-content: center;
   width: 100%;
   margin: 0 0 10px 0;
   color: #000000;
   color: #656565;
   font-size: 13px;

   @media screen and (min-width: 767.9px) {
      font-size: 15px;
     };
     @media screen and (min-width: 1200px) {
      font-size: 17px;
     };
}

.dishListItem {
   position: relative;
   display: flex; 
   padding-left: 3px;
   align-items: center;
   justify-content: space-between;
   height: 60px;
   margin-bottom: 17px;
    transition: all .3s ease-out; 
    border-radius: 15px;
    background-color: #010101;
    box-shadow:
    inset rgba(42, 41, 41, 0.471) -4px -4px 9px,
    inset #303030 4px 4px 9px,
    rgb(0, 0, 0) 3px 3px 8px -3px;
   

   @media screen and (min-width: 768px) {
      height: 65px;
      margin-bottom: 13px;
   };

   @media screen and (min-width: 1200px) {
      height: 70px;
      margin-bottom: 16px;
   };
 }

 .dishListItem:hover {
  
   transition: all .3s ease-out;
   scale: 1.02;
}


 .dishListItemInfo {
   display: flex;
   flex-direction: column;
   height: 60px;
}

.dishListItemInfo span {
   display: flex;
   align-items: center;
   justify-content: start;
 }


 .dishListItemName {
   display: flex;
   height: 20px;
  
   @media screen and (min-width: 767.9px) {
      
   };
   @media screen and (min-width: 1200px) {
      
   };
}


.dishListItemNameDescription {
   display: flex;
   padding: 35px 0 0 10px;
   font-size: 11px;
   letter-spacing: .01px;
   color: #5e5e5e;

   @media screen and (max-width: 374.9px) {
      font-size: 9px;
      letter-spacing: .01px;
   };

   @media screen and (min-width: 767.9px) {
      font-size: 17px;
      padding: 35px 0 0 20px;
   };
   @media screen and (min-width: 1200px) {
