     .toBasketButton {
      position: absolute;
      border: none;
      top: 20px;
      right: 20px; 
      display: flex;
      align-items: center;
      justify-content: end;
      height: 60px;
      width: 150px;
      border: none;
      z-index: 9;
      border-radius: 11px;
      background-color: #000000;
          
      @media screen and (min-width: 767.9px) {
         height: 60px;
         width: 150px;
      };

      @media screen and (min-width: 1200px) {
         height: 60px;
         width: 150px;
      };
   }
   .toBasketLink {
      display: flex; 
      align-items: center;
      justify-content: start;
      height: 40px;
      width: 110px;
      border-radius: 11px;

      @media screen and (min-width: 767.9px) {
         height: 60px;
         width: 150px;
        
       };
       @media screen and (min-width: 1200px) {
         height: 60px;
            width: 150px;
        
       };
   }
   
   .toBasketDescription {
      margin: 0;
      padding: 0;
      font-size: 10px;
      padding-left: 5px;

      @media screen and (min-width: 767.9px) {
         font-size: 14px;
        
       };
       @media screen and (min-width: 1200px) {
         
         font-size: 14px;
       };
   }
   
   .toBasketButton:hover .toBasketDescription {
      color: #5e2000;
     }
   
   .basketIcon {
      position: absolute;
      right: 5px;
      top: 4px;
      fill: #3a3a3a;

      @media screen and (min-width: 767.9px) {
         right: 13px;
         top: 12px;
       };
       @media screen and (min-width: 1200px) {
         right: 13px;
      top: 12px;
       };
     }
     
     .basketIcon.nonEmpty {
        fill: #5e2000;
      }

      .basketBadge {
         position: absolute;
         right: 4px;
         top: 9px;
         display: flex;
         justify-content: center;
         align-items: center;
         height: 12px;
         width: 12px;
         border-radius: 50%;
         color: #dbdbdb;
         background-color: #5e2000;
         font-size: 15px;
         z-index: 2;
         box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.808);

         @media screen and (min-width: 767.9px) {
           
            height: 15px;
         width: 15px;
            right: 11px;
            top: 16px;
          };
          @media screen and (min-width: 1200px) {
            right: 11px;
         top: 16px;
           
          };
        }


