.featuresTop  {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 20px;
  grid-row-gap: 20px;
  width: 100%;
  margin: 0;
  padding: 0 20px;
 
  @media screen and (min-width: 768px) {
   
    grid-column-gap: 25px;
    grid-row-gap: 25px;
    /* width: 1110px; */
    /* height: 450px; */
  };
  @media screen and (min-width: 1200px) {
     grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 30px;
    grid-row-gap: 30px;
    /* width: 1110px; */
    /* height: 450px; */
  };
 
}

  .featuresTop li {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    width: 100%;
    border-radius: 14px;
    background-image: url('images/str2.png'); 
    background-color: #000000;
    background-size: 170%;
    background-position: top;
    background-repeat: repeat;
    background-attachment:unset;
    overflow: hidden;
   
    box-shadow: inset 0px 0px 111px 17px #000000db;
    box-shadow:  4px 4px 15px 4px #0000008b;

    @media screen and (min-width: 768px) {
      border-radius: 17px;
      height: 200px;
    };
    @media screen and (min-width: 1200px) {
      background-size: 80%;
      background-repeat: repeat;
      background-attachment: fixed;
      border-radius: 19px;
      box-shadow:  4px 4px 15px 4px #0000008b;
      height: 200px;
    };
  }

